.aurora-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1;
  background-color: var(--background-default);
  background-image: radial-gradient(circle at bottom, var(--primary-main) 0%, var(--secondary-main) 25%, transparent 50%);
  background-size: 200% 200%; /* Make the gradient larger than the viewport */
  animation: moveGradient 30s infinite alternate;
}

@keyframes moveGradient {
  0% { background-position: 50% 100%; }
  25% { background-position: 75% 125%; }
  50% { background-position: 25% 150%; }
  75% { background-position: 0% 125%; }
  100% { background-position: 50% 100%; }
}